import { version } from "../../package.json";
import { observer } from "mobx-react-lite";
import React, { useContext } from "react";
import { Alert, Button, Col, Container, Image, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import AppState from "../stores/AppState";
import IndicatorsPanel from "./building/IndicatorsPanel";
import Dropzone from "./DropZone.js";
import Footer from "./Footer";
import imglogo from "./img/logo.svg";
import iconclearmodel from "./img/outline-new_document-24px.svg";
import NavBar from "./Nav";

const HelpPage = observer((props) => {
  const appstate = useContext(AppState);

  const handleUpload = (acceptedFiles, rejectedFiles, event) => {
    if (acceptedFiles.length > 0) {
      const file = acceptedFiles[0];
      const reader = new FileReader();
      reader.onload = (rawdata) => {
        if (
          file.name.includes("KyGananciasSolares.txt") ||
          file.path.includes("KyGananciasSolares.txt")
        ) {
          appstate.loadData(rawdata.target.result, "FSHOBST");
        } else if (
          file.name.toLowerCase().includes(".ctehexml") ||
          file.path.toLowerCase().includes(".ctehexml")
        ) {
          appstate.loadData(rawdata.target.result, "CTEHEXML");
        } else {
          appstate.loadData(rawdata.target.result, "JSON");
        }
      };
      reader.readAsText(file);
    }
  };

  const dropHeight = "150px";
  return (
    <Container fluid>
      <NavBar route={props.route} />
      <Row>
        <Col md={3} className="text-center">
          <Image src={imglogo} width="90%" />
          <Alert variant="warning">
            <b>NOTA:</b> Esta aplicacin y la informacin contenida en ella{" "}
            <b>no tienen valor reglamentario</b>.
          </Alert>
        </Col>
        <Col md={9}>
          <Row>
            <Col>
              <h1>
                EnvolventeCTE <small>(v.{version})</small>
              </h1>
              <p>
                Aplicacin para el clculo de indicadores de calidad y
                parmetros descriptivos de la envolvente trmica de los
                edificios para la evaluacin energtica y aplicacin del CTE
                DB-HE (2019):
              </p>
              <ul>
                <li>
                  <b>transmitancia trmica global</b> (<b>K</b>)
                </li>
                <li>
                  <b>tasa de renovacin de aire a 50 Pa</b> (
                  <b>
                    n<sub>50</sub>
                  </b>
                  ) y valor de referencia (n<sub>50,ref</sub>)
                </li>
                <li>
                  <b>parmetro de control solar</b> (
                  <b>
                    q<sub>sol;jul</sub>
                  </b>
                  )
                </li>
                <li>
                  rea til a efectos del clculo de indicadores energticos (A
                  <sub>util</sub>), compacidad (<b>V/A</b>) , volumen bruto (
                  <b>V</b>) , volumen neto (
                  <b>
                    V<sub>int</sub>
                  </b>
                  ), ...
                </li>
              </ul>

              <p>Los distintos apartados del programa permiten:</p>
              <ul>
                <li>
                  <Link to="/building">Edificio</Link>: visualizar, introducir,
                  editar los componentes de la envolvente trmica del edificio.
                </li>
                <li>
                  <Link to="/climate">Clima</Link>: explorar diversos parmetros
                  relacionados con el clima exterior del edificio.
                </li>
                <li>
                  <Link to="/elements">Elementos</Link>: ayudas para el clculo
                  de parmetros descriptivos de algunos elementos de la
                  envolvente trmica.
                </li>
              </ul>
            </Col>
          </Row>
          <Row>
            <Col>
              <h3>
                Parmetros de eficiencia energtica de la envolvente trmica
              </h3>
              <p>Resultados obtenidos para el modelo actual:</p>
              <IndicatorsPanel />
              <Alert variant="info">
                Pulse en el botn de "Detalles" para ms informacin.
              </Alert>
            </Col>
          </Row>
          <Row>
            <Col>
              <h3>Carga de datos</h3>
              <p>
                Pulse este botn para generar un modelo vaco e introducir de
                cero los elementos de la envolvente trmica:
              </p>

              <Button
                variant="secondary"
                block
                onClick={(_e) => appstate.clearModel()}
                title="Pulse para dejar el modelo actual vaco, sin elementos definidos"
                style={{
                  fontSize: 20,
                  height: 50,
                  marginTop: 20,
                  marginBottom: 20,
                }}
              >
                <img src={iconclearmodel} alt="Limpiar modelo" /> Modelo vaco
              </Button>
              <p>
                Arrastre y suelte en el rea inferior o pulse para importar
                datos desde: un archivo <b>.json</b> generado por EnvolventeCTE,
                un archivo <b>.ctehexml</b> de HULC, un archivo{" "}
                <b>KyGananciasSolares.txt</b> de HULC (importacin de factores
                de sombras remotas de los huecos).
              </p>
            </Col>
          </Row>
          <Row style={{ fontSize: 18, height: dropHeight }}>
            <Col>
              <Dropzone
                onDrop={handleUpload}
                accept={"application/json, .json"}
                message={
                  <>
                    <h3>EnvolventeCTE</h3>
                    <p>
                      Importar modelo desde archivo <i>.json</i> de{" "}
                    </p>
                  </>
                }
                containerHeight={dropHeight}
              />
            </Col>
          </Row>
          <Row style={{ paddingTop: 20, fontSize: 18, height: dropHeight }}>
            <Col>
              <Dropzone
                onDrop={handleUpload}
                accept={"application/octet-stream, .ctehexml"}
                message={
                  <>
                    <h3>HULC</h3>
                    <p>
                      Importar modelo desde archivo <i>.ctehexml</i>
                    </p>
                  </>
                }
                containerHeight={dropHeight}
              />
            </Col>
            <Col>
              <Dropzone
                onDrop={handleUpload}
                accept={"text/plain, .txt"}
                message={
                  <>
                    <h3>HULC</h3>
                    <p>
                      Importar factores de sombras remotas de los huecos desde
                      archivo <i>KyGananciasSolares.txt</i>
                    </p>
                  </>
                }
                containerHeight={dropHeight}
              />
            </Col>
          </Row>
        </Col>
      </Row>
      <Footer />
    </Container>
  );
});

export default HelpPage;
